import {mapState} from 'vuex'
export default {
	data() {
		return {
			url:'/txapi/dialogue/index?key=',
			dataobj:''
		}
	},
	computed:{
		...mapState([
			'requesturl',
			'key'
		])
	},
	methods: {
		getdata:function(){
			var _this = this
			uni.request({
				url:_this.requesturl + _this.url + _this.key,
				success:function(data){
					console.log(data)
					_this.dataobj = data.data.newslist[0]
					console.log(_this.dataobj)
				}
			})
		},
		nextfn:function(){
			this.getdata()
		}
	},
	onLoad(){
		this.getdata()
	}
}
